                <p>
                  <img src="~@/assets/img/footer/phone.png" alt="">
                  <span>Tel 0571-86203769 / 15356159698</span>
                </p>
                <p>
                  <img src="~@/assets/img/footer/email.png" alt="">
                  <span>Email davidli898@163.com</span>
                </p>
                <p>
                  <img src="~@/assets/img/footer/address.png" alt="">
                  <span>Address 26818</span>
                </p>
              </div>
            </div>
          </div>
          <div class="address">
            <map-drag lng="120.0280900000" lat="30.2830200000"></map-drag>
          </div>
        </div>
    </div>
</template>
<script>
import mapDrag from '@/components/map/mapDrag'
export default {
  name: 'ContactWay',
  components: {
    mapDrag
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
@import "./contact-way.scss";
</style>



